.accountInvite {
  background-color: #1c1c1f;
  height: 100%;
}
.accountInvite .mainContent .header {
  display: flex;
  justify-content: center;
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
  margin-top: 1.5625rem;
  position: relative;
}
.accountInvite .mainContent .header .leftLogo {
  width: 1.59375rem;
  height: 2.96875rem;
  background-image: url(/img/eternalGateLogo.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.accountInvite .mainContent .header .title {
  font-size: 1.59375rem;
  color: #fff;
  height: 2.96875rem;
  line-height: 2.96875rem;
  margin-left: 0.53125rem;
}
.accountInvite .mainContent .img {
  display: flex;
  justify-content: center;
  padding-top: 0.625rem;
  margin-top: 1.25rem;
}
.accountInvite .mainContent .img img {
  width: 40%;
  height: 2.8125rem;
}
.accountInvite .mainContent .img .zhCn {
  color: #fff;
  font-size: 0.96875rem;
}
.accountInvite .mainContent .welcomeBg {
  display: flex;
  justify-content: center;
  margin-top: -40px;
}
.accountInvite .mainContent .welcomeBg .imgBG {
  width: 100%;
  height: 18.75rem;
  background-image: url(/img/registerSuccess.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.accountInvite .mainContent .qrCode {
  display: flex;
  justify-content: center;
  margin-top: -30px;
}
.accountInvite .mainContent .qrCode canvas {
  width: 9.375rem !important;
  height: 9.375rem !important;
}
.accountInvite .mainContent .bottom {
  padding-top: 1.25rem;
  padding-bottom: 0.625rem;
  display: flex;
  justify-content: center;
  color: #fff;
}
.accountInvite .mainContent .interent {
  padding-top: 1.25rem;
  padding-bottom: 0.625rem;
  display: flex;
  justify-content: center;
  color: #ccc;
}
